// Provides global ajax bindings
//
// Available events are:
//
// ```
// ajax:start
// ajax:stop
// ```
// Ajax options can be set at `models/config` using the provided
// `ajax`object
//
'use strict';

import storeError from './storeError';
import storeSuccess from './storeSuccess';
import $ from 'jquery';

//
// bind global ajax Error / Success handlers to helpers
//
$(document).ajaxError(storeError);
$(document).ajaxSuccess(storeSuccess);
